.btn {
	--max-width: auto;
	--bs-btn-color: #{$wh};
	--bs-btn-font-size: 0.875rem;
	--bs-btn-font-weight: 600;
	--bs-btn-padding-y: 0.75rem;
	--bs-btn-padding-x: 1.375rem;
	--bs-btn-border-radius: 0.875rem;
	max-width: var(--max-width);
	white-space: nowrap;
	&:hover {
		box-shadow: 0 0 0 0.25rem rgba(49, 132, 253, .5); }
	.icon {
		width: 1.25rem;
		height: 1.25rem;
		&.icon-left {
			margin-right: 0.375rem; }
		&.icon-right {
			margin-left: 0.375rem; } }
	&.btn-dark {
		--bs-btn-bg: #{$bl-1}; }
	&.btn-lg {
		font-size: 1.125rem;
		font-weight: 700;
		padding: 1.125rem 2.5rem;
		border-radius: 1.125rem; } }

$bem: '.btn-play';
.btn-play {
	position: absolute;
	color: $wh;
	z-index: 9;
	& &-icon {
		width: 3.5rem;
		height: 3.5rem;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		background-color: $pr;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 0.625rem;
		box-shadow: 0 0 0 0.3125rem rgba($wh, .4);
		transition: all .2s ease-in-out;
		.icon {
			right: -0.125rem;
 } }			// transition: all .1s ease-in-out
	& &-text {
		font-size: 0.625rem;
		text-transform: uppercase;
		text-shadow: 0 0.0625rem 0 rgba(#000, .55);
		color: $wh !important; }
	&:hover {
		box-shadow: 0 0 0 0 rgba(49, 132, 253, 0); }
	&:active,
	&:focus,
	&:hover {
		color: $pr;
		#{$bem}-icon {
			background-color: $wh; } } }

