.container,
.container-lg,
.container-md,
.container-sm,
.container-xl,
.container-xxl {
	--bs-gutter-x: 3.125rem;
	position: relative;
	@include rmin(1400) {
		max-width: 1250px; } }

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
	--bs-heading-color: #{$default-text-color}; }


.carousel-indicators {
	[data-bs-target] {
		border-radius: 0.25rem;
		width: 1.75rem;
		&.active {} } }

.tooltip {
	& &-inner {
		font-size: 0.8125rem;
		padding: 0.625rem 0.3125rem; } }
