.nav-header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	padding: 3.125rem 0;
	z-index: 99;
	border-bottom: 0.0625rem solid transparent;
	@include lg {
		padding: 1.875rem 0;
		position: absolute; }
	& > div > * {
		z-index: 2; }
	.container {
		position: initial; }
	&.toggle-fixed-navbar {
		padding: 0.625rem 0;
		border-bottom-color: #eee;
		background-color: $wh;
		@include lg {
			position: absolute;
			.container {
				position: initial; }
			.btn-order {
				bottom: 1.25rem; } } }
	.navbar-brand {
		color: $bl;
		font-size: 1.375rem;
		font-weight: 900;
		@include sm {
 }			// font-size: 1.125rem
		.logo-img {
			margin-right: 1.125rem;
			@include sm {
				margin-right: 0.9375rem; } } }
	.btn-order {
		order: 1;
		@include lg {
			display: none; } }
	.navbar-toggler {
		@include lg {
			order: 2; } }
	.navbar-nav {}
	.nav-item {
		--padding-x: 1.375rem;
		padding-left: var(--padding-x);
		padding-right: var(--padding-x); }
	.nav-link {
		--bs-nav-link-color: #{$gray};
		--bs-nav-link-font-size: 1rem;
		--bs-nav-link-font-weight: 600;
		--bs-navbar-nav-link-padding-x: 0;
		@include lg {
			--bs-nav-link-font-size: 1.5rem;
			--bs-nav-link-padding-y: 0.875rem; }
		&::before {
			--bg: #{$pr}; }
		&.active {}
		&:focus,
		&:hover {
			--bs-nav-link-hover-color: #{$pr}; } }
	.navbar-collapse.collapsing,
	.navbar-collapse {
		@include lg {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			text-align: center;
			padding-top: var(--padding-nav);
			background-color: var(--body-bg);
			z-index: 1;
			height: 100vh; } } }
