.footer {
	font-size: 0.75rem;
	border-top: 0.0625rem solid $gray-3;
	padding: 1.875rem 0;
	margin-top: 4.375rem;
	@include md {
		padding-bottom: 12.5rem; }
	& > div {
		justify-content: space-between;
		@include lg {
			justify-content: center; } }
	& &-copyright {
		margin: 0; }
	.nav {
		@include lg {
			justify-content: center;
			margin-top: 0.625rem; }
		&-link {
			--bs-nav-link-color: #{$bl-2};
			@include lg {
				padding: 0.25rem 0.625rem; } } } }
