	& &-video-iframe {
		border-radius: var(--bdrs-avatar);
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 9;
		overflow: hidden;
		&.before::before {
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			z-index: 9; }
		iframe {
			width: calc(100% + 30rem);
